@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  font-family: 'Ubuntu', sans-serif;
  margin: 0;
  background: cornflowerblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
.App {
  text-align: center;
  padding: 20px;
}

.input_wrapper {
  display: flex;
  max-width: 350px;
  align-items: center;
  margin: 0 auto;
}
.input_item {
  margin: 2rem 0;
  width: 80%;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
  padding: 15px 25px;
  background-color: #eee;
}
.input_btn {
  display: block;
  padding: 15px 25px;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
}
.input_btn:hover {
  box-shadow: inset 0 -1px 1px rgba(0, 0, 0, 0.776);
}

.todo_item{display:flex;align-items:center;margin:2rem auto;border:1px solid black;border-radius:5px;box-shadow:2px 3px 5px rgba(0,0,0,0.3);max-width:350px;padding:15px 25px;background-color:#eee}.todo_item input{margin-right:20px}.todo_item .description{font-size:20px;color:darkblue}.todo_item .desc_checked{color:grey;text-decoration:line-through;font-size:20px}.todo_item span{display:none;font-size:20px}.todo_item:hover span{display:block;margin-left:auto;cursor:pointer}

